@import "~antd/dist/antd.css";

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}


.App-header {
  background: rgb(246,244,163);
background: linear-gradient(36deg, rgba(246,244,163,1) 0%, rgba(239,154,154,1) 79%);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  font-size: 1.8em;
  font-weight: 200;
}

.App-Title{
  letter-spacing: 5px;
  margin: 1em;
  font-size: 1.2em;
  font-weight: 250;
}